
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin:0px auto 0;
  width:1000px;
  max-width:100%;
  background-color:white;
}
body {
  background-image: url(/static/img/textured_paper.3c0f6fd.png);
}
.outer-wrapper {
	float:left;
	width:100%;
}
.inner-wrapper {
  background-color:white;
  float:left;
  width:100%;
  padding:0 14px;
}
.header {
  padding:20px 0 0;
  float:left;
  width:100%;
}
.tab-wrapper {
	float:left;
	width:100%;
	margin: 0 0 20px;
}
.header.porto {
	padding-top: 20px;
	margin-top:20px;
}
.inner-wrapper.porto {
	padding-top:20px;
}
.company-wrapper {
  -webkit-box-shadow: 0px 0px 20px 3px #888888;
          box-shadow: 0px 0px 20px 3px #888888;
  width:100%;
  float:left;
  background-color:#ffffff;
}
.crc {
  margin:70px 0 50px;
}
.crc .item-tab {
  min-height: 540px;
}
.sitespirit {
  margin:0 0 30px;
}
.header.porto,
.inner-wrapper.porto {
}
.header .logo-img {
  margin:0 0 14px;
}
#contact {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 0 10px;
    z-index: 100;
    background-color: #626266;
    color:white;
}
#contact a {
  color:white;
  text-decoration:underline;
}
#contact a:hover {
  text-decoration:none;
}

