  width:40px;
  height:40px;
  line-height:40px;
  color:white;
  font-size:22px;
}
.big-image[data-v-5aa5f872]:hover {
  cursor:nesw-resize;
  opacity:0.6;
}
.item_images img[data-v-5aa5f872] {
  max-width:100%;
}
.item_images_thumb[data-v-5aa5f872] {
  float:left;
  width:100%;
  padding: 0 0 10px;
  border-bottom: 1px solid black;
  margin:0 0 10px;
}
.item_images_thumb img[data-v-5aa5f872] {
  border:1px solid transparent;
}
.thumb-image[data-v-5aa5f872] {
  max-height:100px;
  float:left;
  width:23.5%;
  margin-right:2%;
  margin-bottom:14px;
  overflow:hidden;
  opacity:0.4;
  cursor: pointer;
}
.thumb-image[data-v-5aa5f872]:hover,
.thumb-image.active[data-v-5aa5f872] {
  /*border:1px solid black;*/
  opacity:1;
}
.thumb-image img[data-v-5aa5f872] {
  width:100%;
}
.item_images_thumb img[data-v-5aa5f872]:nth-child(4n + 4) {
  margin-right:0;
}
.item_images_thumb img[data-v-5aa5f872]:nth-child(4n + 5) {
  clear:left;
}
/* popup */
.modal-dialog[data-v-5aa5f872] {
  max-width:100%;
  width:1200px;
}
#screenshot-modal img[data-v-5aa5f872] {
 max-width:100%;
}

